@import 'imports/vars.scss';

header { border-top: 4px solid #008ece;}

.top-header,
.main-navigation {

	width: 100%;
	max-width:1060px;
	margin: 0 auto;
}

.top-header { 

	display: flex;
	color: rgba(59, 92, 123, 0.5);
	justify-content: space-between;
	padding-top: 57px;

}

.widget.social-links { display: flex;

	.soclink {

	    width: 40px;
	    height: 40px;
	    color: rgba(59, 92, 123, 0.5);
	    border: 1px solid rgba(59, 92, 123, 0.1);
	    border-right: 0;
	    text-align: center;
	    padding: 8px 0;

	    &:last-of-type { border-right:  1px solid rgba(59, 92, 123, 0.1); }

	    &:hover { color: rgba(0, 142, 206, 0.5); }
	}
}

.search-form {

    width: 307px;
    display: flex;

   .searchstring {
   
        width: calc(100% - 40px);
       	font: 14px/0.6px $font-sans;
       	color: rgba(59, 92, 123, 0.5);
        background: transparent;
        border: 1px solid rgba(59, 92, 123, 0.1);
        border-radius: 0;
        padding: 10px;
         
        	&::-moz-placeholder {

	        	 color: rgba(59, 92, 123, 0.8);
	        	
        	}

        	&::-webkit-input-placeholder {

        		color: rgba(59, 92, 123, 0.8);
        		
        	}   
    }
}

.searchsubmit {
   
        width: 40px;
        height: 41px;
        vertical-align: top;
        border: 1px solid rgba(59, 92, 123, 0.1);
        border-left: 0;
        background-color: transparent;
        border-radius: 0;
        color: #b0b7c2;
        cursor: pointer;

        &:hover {  color: rgba(0, 142, 206, 0.5); }
}

.sign-search {
    padding-top: 5px;

     @media screen and (max-width: 768px) { display: none; }
}

.main-navigation {

    display: flex;
    flex-direction: column;
    border-bottom: 1px solid;
    margin-bottom: 30px;

    @media screen and (max-width: 1023px) { display: none; }

    ul.menu { 

    	width: 100%;
    	display: flex; 
    	justify-content: space-between;
   	    padding: 0 20px;

        li.menu-item  { 

            transition:  all 0.3s;
            position: relative;

            &:hover > ul { display: block; }

            ul { display: none; }
           
        }

        a, span {

			display: block;
            font: 14px $font-sans;
            color: rgba(72, 92, 121, 0.8);
            text-transform: uppercase;
            padding: 46px 0 18px;

            &:hover  { color: #008ece; }

        }
        .current-item { border-bottom: 4px solid #008ece;}

        span {display: inline-block; }

        li ul {
        
            min-width: 230px;
            position: absolute;
            top: 100%;
            margin: 0;
            z-index: 2;

            li.menu-item:hover { background-color: #2f8fc8; }
            
            li.menu-item::before { font-size: 0; }

            li ul { 
                
                left: 100%;
                top: 0;     
            }
        }
    }
}

.slicknav_btn.slicknav_collapsed {
    display: none;
}